
import { watch } from "vue";
import { Options, Vue } from "vue-class-component";
import { mapGetters } from "vuex";
import { LocationQueryRaw } from "vue-router";

import type { PaginationQuery } from "@/types/pagination";

import PageContent from "@/components/internal/common/PageContent.vue";
import InputField from "@/components/snap-ui/InputField.vue";
import Pagination from "@/components/ui/Pagination.vue";
import UserList from "@/components/internal/users/UsersList.vue";

import UserCreateForm from "@/components/internal/users/UserCreateForm.vue";
import UserEditForm from "@/components/internal/users/UserEditForm.vue";
import UserEditRolesForm from "@/components/internal/users/UserEditRolesForm.vue";
import UserEditHeader from "@/components/internal/users/UserEditHeader.vue";
import SidePanel from "@/components/ui/SidePanel.vue";

import { MultiplePermissionsChecker } from "@/store/user.module";
import { FeatureToggle } from "@/store/treatments.module";

type UserOperation = "edit" | "permissions" | "create" | undefined;

@Options<Users>({
  props: {
    operation: String,
    id: String,
  },
  components: {
    InputField,
    Pagination,
    UserList,
    UserCreateForm,
    UserEditForm,
    UserEditRolesForm,
    UserEditHeader,
    SidePanel,
    PageContent,
  },
  computed: {
    ...mapGetters(["hasEveryPermissions", "featureOn"]),
  },

  beforeRouteUpdate(to, from, next) {
    this.checkOperation(to.params);
    next();
  },
})
export default class Users extends Vue {
  operation?: UserOperation;
  id?: string;

  forms: Array<{
    operation: UserOperation;
    panel: SidePanel;
    permissions: Array<string>;
    requiredId: boolean;
  }> = [];

  query: PaginationQuery = {
    search: "",
    currentPage: 0,
    pageSize: window.innerWidth > 1500 ? 48 : 24,
    nextPage: false,
  };

  hasEveryPermissions?: MultiplePermissionsChecker;
  featureOn?: FeatureToggle;

  get userEditFormPermissions(): Array<string> {
    return this.featureOn?.(
      "USER-434-allow-impersonation-without-user-edit-permission"
    )
      ? ["user:read"]
      : ["user:edit"];
  }

  get canEditUser(): boolean {
    return !!this.hasEveryPermissions?.(this.userEditFormPermissions);
  }

  get queryParams(): LocationQueryRaw {
    return this.featureOn?.("USER-455-sync-users-query-with-url")
      ? {
          search: this.query.search || undefined,
          currentPage: this.query.currentPage || 0,
        }
      : {};
  }

  async beforeMount() {
    await this.$store.dispatch("useTreatments");

    this.forms = [
      {
        operation: "create",
        panel: this.$refs.userCreatePanel as SidePanel,
        permissions: ["user:create"],
        requiredId: false,
      },
      {
        operation: "edit",
        panel: this.$refs.userEditPropertiesPanel as SidePanel,
        permissions: this.userEditFormPermissions,
        requiredId: true,
      },
      {
        operation: "permissions",
        panel: this.$refs.userEditPermissionsPanel as SidePanel,
        permissions: ["role:read", "permission:read"],
        requiredId: true,
      },
    ];

    this.checkOperation({ operation: this.operation, id: this.id });

    if (this.featureOn?.("USER-455-sync-users-query-with-url")) {
      if (this.$route.query.search || this.$route.query.currentPage) {
        this.query.search = this.$route.query.search as string;
        this.query.currentPage =
          parseInt(this.$route.query.currentPage as string) || 0;
      }

      watch(this.query, () => {
        this.$router.replace({ query: this.queryParams });
      });
    }
  }

  reset(): void {
    this.query.currentPage = 0;
  }

  checkOperation({
    operation,
    id,
  }: {
    operation: UserOperation;
    id: string | undefined;
  }): void {
    this.forms.forEach((form) => {
      if (form.panel && this.hasEveryPermissions?.(form.permissions)) {
        if (form.operation == operation) {
          if (!form.requiredId || (form.requiredId && id)) form.panel.open();
          else {
            form.panel.close(false);
          }
        } else {
          form.panel.close(false);
        }
      }
    });
  }
}
